@use "../../css/variable.scss";
.header_top {
  width: 100%;
  height: 110px;
  background-color: white;
  overflow: hidden;
  &.sticky {
    position: -webkit-sticky; /* 사파리 지원 */
    position: sticky;
    z-index: 20;
    top: 0;
  }
  .material-symbols-outlined {
    font-variation-settings: "FILL" 0, "wght" 150, "GRAD" 0, "opsz" 24;
    cursor: pointer;
    font-size: 50px;
    &:hover {
      font-variation-settings: "FILL" 0, "wght" 250, "GRAD" 0, "opsz" 24;
      color: variable.$main-color;
    }
  }
  .material-symbols-rounded {
    font-variation-settings: "FILL" 0, "wght" 700, "GRAD" 0, "opsz" NaN;
    cursor: pointer;
    font-size: 50px;
  }
  h1 {
    height: 50px;
    a,
    img {
      height: 100%;
    }
  }
  .wrap {
    height: 50%;
    gap: 20px;
  }
  #user-search {
    width: 100%;
    border-radius: 30px;
    height: 100%;
    padding: 0 20px;
    border: 1px solid variable.$gray;
    outline: none;
    &:focus {
      border: 2px solid variable.$main-color;
    }
  }

  .button_area {
    flex-shrink: 0;
  }

  // bottom nav
  .bot {
    position: fixed;
    width: 100%;
    bottom: 0;
    padding-top: 5px;
    padding-bottom: 5px;
    background-color: white;
    box-shadow: 0 1px 7px 0 variable.$gray; // 네임스페이스 사용
  }

  // breakpoint
  @media screen and (max-width: variable.$tablet) {
    height: clamp(50px, 5vw, 80px);
    .wrap {
      height: 80%;
    }
    h1 {
      height: 100%;
    }
  }
}
